.pag__table {
    font-family: Arial, Helvetica, sans-serif;
    border-collapse: collapse;
    width: 100%;
    border: 1px solid red;
    margin-top: 15px;
}


.pag__table th{
    padding-top: 12px;
    padding-bottom: 12px;
    text-align: left;
    background-color: #04AA6D;
    color: white;
    text-transform: uppercase;
}


.buttons button.active {
    background-color: red;
    color: white;
}

