
.login-container {
        padding: 10px;
        border-radius: 5px;
        text-align: center;
        width: 300px;
        margin: auto;
        margin-top: 200px;
}
.login-container h2 {
        color: #333;
}
.login-container input {        
        width: 100%;
        padding: 10px;
        margin: 10px 0;
        border: 1px solid #ccc;
        border-radius: 5px;
}
.login-container button {
        background-color: #007bff;
        color: #fafafa;
        font-weight: bold;
        padding: 10px 20px;
        border: none;
        border-radius: 10px;
        cursor: pointer;
}