.calculator {
    width: 250px;
    margin: auto;
    padding: 20px;
    border: 2px solid #ccc;
    border-radius: 5px;
    background-color: #f5f5f5;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.calculator input[type="text"] {
    width: 240px;
    height: 40px;
    font-weight: 600;
    margin: 10px auto;
}

.calculator  button {
    width: 48px;
    height: 48px;
    font-size: 20px;
    margin: 5px;
    border: 1px solid #ccc;
    border-radius: 5px;
    cursor: pointer;
}

.calculator button:hover {
    background-color: #ccc;
}

.calculator-equal {
    width: 96px;
    height: 48px;
    margin: 5px;
    border: 1px solid #ccc;
    border-radius: 5px;
    cursor: pointer;
    text-align: center;
    padding: 20px;
}